<template>
  <div class="container">
    <div class="item" v-for="(val,index) in articles">
      <a href="javascript:void(0)" @click="jumpCategory(val.articleId,index)">
        <h3>{{ val.articleTitleName }}</h3>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;{{ val.articleIntroduction }}</p>
      </a>
      <hr>
    </div>
  </div>
</template>

<script>
export default {
  name: "Article",
  data() {
    return {
      articles: []
    };
  },
  methods: {
    jumpCategory(articleId, index) {
      this.$router.push({
        name: "Info",
        query: {
          articleId: articleId
        }
      });

      var article = this.articles[index];
      this.$route.meta.title=article.articleTitleName;
      this.$route.meta.titleName = article.articleTitleName;
      this.$route.meta.authorName = article.authorName;
      this.$route.meta.publishDate = article.publishDate;
      this.$route.meta.articleBrowseCount = article.articleBrowseCount;
      this.$route.meta.articleLikeCount = article.articleLikeCount;
    }
  },
  mounted: function() {

  this.$fetch('/portal/article?articleType=1')
      .then((response) => {
        this.articles=response.result;
      })

    // this.articles = [
    //   {
    //     articleId: 20000908,
    //     articleTitleName: "!",
    //     publishDate: "2019-03-31 09:13:56",
    //     articleIntroduction:
    //       "1232ifelse if ",
    //     authorName: "",
    //     articleBrowseCount: "1530",
    //     articleLikeCount: "885"
    //   },
    //   {
    //     articleId: 20000909,
    //     articleTitleName: "cms  !",
    //     publishDate: "2019-03-31 09:13:56",
    //     articleIntroduction: "js",
    //     authorName: "",
    //      articleBrowseCount: "1530",
    //     articleLikeCount: "885"
    //   },
    //   {
    //     articleId: 20000912,
    //     articleTitleName: "git!",
    //     publishDate: "2019-03-31 09:13:56",
    //     articleIntroduction: "git,,!",
    //     authorName: "",
    //      articleBrowseCount: "1530",
    //     articleLikeCount: "885"
    //   }
    // ];
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin-top: 6%;
}
p {
  margin-top: 2%;
}
a:link {
  color: #2c3e50;
  text-decoration: none;
}
a:visited {
  color: #2c3e50;
  text-decoration: none;
}
a:hover {
  color: #727880;
  text-decoration: none;
}
</style>


