<template>
  <div class="container">
    <div class="text-center">
      <div class="item_c" v-for="(val,key) in categorys">
        <a href="javascript:void(0)" @click="jumpCategory(val.categoryCode)">
          <h3>{{ val.categoryName }}</h3>
          <p>{{ val.categoryDes }}</p>
        </a>
        <hr>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Home",
  data() {
    return {
      categorys: []
    };
  },
  methods: {
    jumpCategory(categoryCode) {
      this.$router.push({
        name: categoryCode,
      });
    }
  },
  mounted: function() {
    this.categorys = [
      {
        categoryName: "  ",
        categoryCode: "Life",
        categoryDes:
          ""
      },
      {
        categoryName: "  ",
        categoryCode: "Article",
        categoryDes:
          ""
      },
      {
        categoryName: "VUE",
        categoryCode: "HelloWorld",
        categoryDes:
          "Vue "
      }
    ];
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin-top: 6%;
}
p {
  margin-top: 2%;
}
a:link {
  color: #2c3e50;
  text-decoration: none;
}
a:visited {
  color: #2c3e50;
  text-decoration: none;
}
a:hover {
  color: #727880;
  text-decoration: none;
}
</style>


